.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* navbar */
.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  padding: 20px 0;
  z-index: 1000;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
}

.navbar ul {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}

.navbar ul li {
  margin: 0 20px;
}

.navbar ul li a {
  text-decoration: none;
  color: #000;
  font-weight: bold;
  font-size: 1.2rem;
}

.navbar ul li a:hover {
  color: #e60023; /* Red color on hover, matching the design */
}

/*sidebar*/

.sidebar {
  position: fixed;
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  padding: 10px;
  background-color: #fff;
  z-index: 1000;
}

.sidebar ul {
  list-style: none;
  padding: 0;
  margin:0;
}

.sidebar ul li {
  margin: 15px 0;
}

.sidebar ul li a {
  text-decoration: none;
  color: #000;
}


.sidebar ul li a:hover {
  color: #e60023; /* Red color on hover */
}

.main-content {
  margin-top: 80px; /* Adjust based on navbar height */
  margin-left: 60px; /* Adjust based on sidebar width */
  padding: 20px;
}

/*portfolio*/

.portfolio-page {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  height: 100vh;
  background-color: #f7f7f7; /* Match this with your design's background color */
}

.intro {
  flex: 1;
}

.intro h1 {
  font-size: 3rem;
  font-weight: bold;
}

.intro h2 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.intro p {
  font-size: 1.2rem;
}

.hero-image {
  flex: 1;
  display: flex;
  justify-content: center;
}

.hero-image img {
  max-width: 100%;
  height: auto;
}

